







































.virus{
  height: 100vh;
  background: url(../assets/imgs/virus/bg.png);
  position: relative;
  >.top{
    height: 70px;
    line-height: 70px;
    text-indent: 30px;
    font-size: 25px;
    color: #fff;
    background: url(../assets/imgs/hole/shang.png);
  }

  .content{
    height: 100%;
    .left{
      display:inline-block;
      width: 13%;
      height: 100%;
      vertical-align: top;
      background: url(../assets/imgs/hole/zuo.png);
      ul{
        li{
          height: 35px;
          line-height: 35px;
          color: #fff;
          text-align: center;
          margin: 10px 0;
        }
        li:hover{
          cursor: pointer;
        }
        .router-link-active{
          background: #0B3D91;
        }
      }
    }


    .right{
      display: inline-block;
      width: 87%;
      .tiao{
        height: 70px;
        background: url(../assets/imgs/hole/tiao.png);
        background-position: center;
        color: #fff;
        line-height: 70px;
        text-indent: 40px;
        font-weight: bold;
      }
    }
  }

}
